/************** ItemCard **************/

.searchItemsCategoriesIcon {
  background-image: url(../assets/itemsCategoriesIcons/empty.png);
  background-size: contain;
  background-repeat: no-repeat;
  width: 40px;
  height: 40px;
  display: inline-block;
  margin-left: 20px;
  margin-right: 20px;
  flex: none;
}
.searchItemsContainer {
  border-bottom: solid 2px var(--background-gray);
  cursor: pointer;
  padding-bottom: 10px;
  padding-top: 10px;
  background-color: var(--background-kinda-white);
  display: flex;
  align-items: center;
  text-align: right;
}
.searchItemsContainer:hover {
  background-color: var(--second-color);
  color: white;
}
.searchItemsContainer:hover p {
  color: white;
}

.searchItemsName {
  margin: 0;
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
  white-space: break-spaces;
}

.searchItemsCategories {
  margin: 0;
  font-size: calc(10px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-gray);
}
.searchItemsInfo {
  margin: 0;
  font-size: calc(11px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
}
.searchItemsInfo span {
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bolder;
}
.searchItemsVerified {
  width: 18px;
  height: 18px;
  vertical-align: middle;
  margin-right: 5px;
}
