/************** FavoritePage **************/

#favoritePage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: padding, opacity;
  display: grid;
  height: var(--vh);
}
.favoriteGridWithNav {
  grid-template-rows: 0fr 0fr 1fr 80px;
}
.favoriteGridWithoutNav {
  grid-template-rows: 0fr 0fr 1fr;
}
@media (max-width: 450px) {
  .favoriteGridWithNav {
    grid-template-rows: 0fr 0fr 1fr 65px;
  }
}
@media (min-width: 740px) {
  .favoriteGridWithNav {
    grid-template-rows: 0fr 0fr 1fr;
  }
}
#favoritePageHeader {
  position: sticky;
  top: 0;
  background-color: var(--main-color);
  height: 120px;
  box-shadow: 0px 0px 7px 0px rgba(0, 0, 0, 0.3);
  z-index: 2;
}
#favoritePageHeaderContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  margin: auto;
  padding: 10px 0;
}
#favoritePageTitle {
  color: var(--text-white);
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  margin: 0;
  line-height: 40px;
}
#favoritePageBack {
  width: 35px;
  height: 35px;
  fill: white;
  cursor: pointer;
}
#favoritePageBack:hover {
  fill: black;
}
#favoritePageTabsContainer {
  display: inline-block;
  position: absolute;
  bottom: 5px;
  right: 0;
}
#paddle {
  height: 5px;
  width: 50%;
  position: absolute;
  bottom: -5px;
  right: 0px;
  background-color: white;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
#favoritePageSort {
  position: absolute;
  left: 15px;
  bottom: 10px;
  width: 35px;
  height: 35px;
  fill: white;
  cursor: pointer;
}

#favoriteSearchSvg {
  position: absolute;
  left: 65px;
  bottom: 10px;
  width: 34px;
  height: 34px;
  fill: none;
  cursor: pointer;
}
#favoritePagePopUp {
  position: absolute;
  top: 107px;
  left: 27px;
  background-color: var(--background-gray);
  border-radius: 15px;
  box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
  transform: scale(0);
  transform-origin: 0 0;
  transition: transform 200ms;
}
.favoritePagePopUpItems {
  padding: 10px 30px;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  text-align: center;
  cursor: pointer;
}
.favoritePagePopUpItems:hover {
  color: var(--main-color);
}
.favoritePageTabs {
  margin: 0;
  color: var(--text-white);
  padding: 0 30px;
  display: inline-block;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  cursor: pointer;
  line-height: 40px;
}

.favoritePagerPages {
  max-width: var(--max-width);
  height: 100%;
  overflow: auto overlay;
}
@media (hover: hover) and (pointer: fine) {
  .favoritePagerPages::-webkit-scrollbar {
    width: 14px;
  }
  .favoritePagerPages::-webkit-scrollbar-track {
    box-shadow: inset 0 0 14px 14px transparent;
    border: solid 4px transparent;
  }
  .favoritePagerPages::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 14px 14px #b3b3b3;
    border: solid 4px transparent;
    border-radius: 14px;
  }
  .favoritePagerPages::-webkit-scrollbar-button {
    display: none;
  }
}

#favoriteSearchContainer {
  position: sticky;
  top: 120px;
  width: 100%;
  height: 0px;
  padding: 0px;
  background-color: var(--main-color);
  overflow: hidden;
  z-index: 1;
  box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.3);
}
#favoriteSearchBar {
  display: block;
  margin: auto;
  width: 90%;
  height: 45px;
  padding-right: 50px;
  padding-left: 10px;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px #00000040;
  background-color: var(--background-white);
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: calc(100% - 20px) 50%;
  font-size: 18px;
  color: var(--text-kinda-black);
  transition: top 500ms;
}
#favoritePageBodyContainer {
  overflow: hidden;
  white-space: nowrap;
  will-change: scroll-position;
}
.favoritePageIsEmpty {
  margin: auto;
  margin-bottom: 10%;
  text-align: center;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
}
#lottieEmptyFood,
#lottieEmptyMeal {
  display: block;
  width: 50%;
  margin: auto;
  margin-top: 20%;
}
#lottieNoResultsMeal,
#lottieNoResultsFood {
  width: 50%;
  margin: auto;
  margin-top: 20%;
}
@media (max-width: 450px) {
  #favoritePageBack {
    width: 30px;
    height: 30px;
  }
  #favoritePageSort {
    width: 30px;
    height: 30px;
  }
  #favoriteSearchSvg {
    width: 29px;
    height: 29px;
  }
}
