/************** MealViewer **************/

#mealViewerPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: padding;
}
#mealViewerHeaderButtonsContainer {
  position: relative;
  width: 100%;
  height: 175px;
  background-color: var(--second-color);
}
#mealViewerHeaderItemName {
  position: absolute;
  bottom: 20px;
  right: 20px;
  color: var(--text-white);
  margin: 0;
  font-weight: bold;
  font-size: calc(25px + 6 * ((var(--vw) - 320px) / 680));
  width: 75%;
}
#mealViewerPageHeader {
  position: sticky;
  top: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr) 0.4fr;
  grid-template-rows: auto;
  align-items: center;
  direction: ltr;
  padding: 20px;
  background-color: var(--second-color);
  z-index: 1;
}
#mealViewerHeaderTitle {
  color: white;
  margin: 0;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  grid-column-start: 3;
}
#mealViewerBackButton {
  fill: white;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
#mealViewerBackButton:hover {
  fill: black;
}
#mealViewerEdit {
  fill: white;
  width: 40px;
  height: 40px;
  justify-self: end;
  cursor: pointer;
}
#mealViewerEdit:hover {
  fill: black;
}
#mealViewerTrashCan {
  fill: white;
  width: 40px;
  height: 40px;
  justify-self: end;
  cursor: pointer;
}
#mealViewerTrashCan:hover {
  fill: black;
}
#mealViewerHeaderTitle {
  color: white;
  margin: 0;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: end;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#mealViewerOkButton {
  fill: white;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
#mealViewerOkButtonBg {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: -27px;
  left: 30px;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background-color: var(--main-color);
}
#mealViewerOkButtonAnimation1 {
  position: absolute;
  bottom: -27px;
  left: 30px;
  width: 55px;
  height: 55px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
  animation: PulsAnimation 4s ease 1s infinite normal forwards;
}
#mealViewerOkButtonAnimation2 {
  position: absolute;
  bottom: -27px;
  left: 30px;
  width: 55px;
  height: 55px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
  animation: PulsAnimation 4s ease 1.3s infinite normal forwards;
}
#mealViewerKcal {
  padding: 30px 40px 25px 0px;
  margin: 0;
  font-weight: bold;
  font-size: 40px;
  color: var(--text-kinda-black);
  background-color: var(--background-white);
}
#mealViewerKcal span {
  font-weight: normal;
  font-size: 20px;
  margin-right: 10px;
}
#mealViewerSelectMealContainer {
  background-color: var(--background-white);
  padding-bottom: 20px;
}
#mealNutritionInfoPercentContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 45px 0;
  background-color: var(--background-white);
}
#mealNutritionInfoPercentContainer div {
  width: 22%;
}
.mealPercentTitles {
  text-align: center;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
}
.mealViewerInfoTitles {
  margin: 0;
  margin-top: 15px;
  padding: 20px;
  color: var(--text-kinda-black);
  font-weight: bold;
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  background-color: var(--background-white);
}
@media (max-width: 450px) {
  #mealViewerBackButton {
    width: 30px;
    height: 30px;
  }
  #mealViewerEdit {
    width: 35px;
    height: 35px;
  }
  #mealViewerTrashCan {
    width: 35px;
    height: 35px;
  }
}
