#container {
  position: fixed;
  width: 80%;
  max-width: calc(var(--max-width) - 10%);
  padding: 20px;
  background-color: var(--background-white);
  border-radius: 5px;
  border: none;
  text-align: center;
  box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
}

#container::backdrop {
  background: rgba(0, 0, 0, 0.5);
}

#message {
  margin: auto;
  margin-bottom: 35px;
  color: var(--text-kinda-black);
  font-size: calc(17px + 6 * ((var(--vw) - 320px) / 680));
  text-align: center;
  user-select: none;
}

.buttons {
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  background-color: var(--main-color);
  color: white;
  padding: 3px 40px;
  border: none;
  border-radius: 5px;
  margin: 0 15px;
  cursor: pointer;
  transition: transform 200ms;
}
.buttonsRed {
  background-color: red;
}
.buttons:hover {
  transform: scale(1.05);
}

#container[open] {
  animation: show 0.2s ease normal;
}
@keyframes show {
  from {
    transform: translateY(70%);
    opacity: 0;
  }
  to {
    transform: translateY(0%);
    opacity: 1;
  }
}

#container[open]::backdrop {
  animation: BGshow 0.2s ease normal;
}
@keyframes BGshow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

#container.hide {
  animation: hide 0.2s ease-in normal;
}
@keyframes hide {
  to {
    transform: translateY(70%);
    opacity: 0;
  }
}

#container.hide::backdrop {
  animation: BGhide 0.2s ease-in normal;
}
@keyframes BGhide {
  to {
    opacity: 0;
  }
}
