/************** NavigationBar **************/

#navigationBar {
  position: fixed;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  margin: 0;
  height: 80px;
  width: 100%;
  min-width: 320px;
  max-width: var(--max-width);
  text-align: center;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: var(--background-white);
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  z-index: 3;
  box-shadow: 0px -5px 10px -5px #00000040;
  touch-action: none;
}
.navElements {
  all: unset;
  position: relative;
  display: inline-flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  cursor: pointer;
}
.navElements p {
  color: var(--main-color);
  margin: 0;
  font-size: 0px;
  transform-origin: bottom;
  opacity: 0;
  transition: all 0.2s;
  will-change: font-size, opacity;
}
.navElementsActive p {
  font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
  opacity: 1;
}
.navElements svg {
  width: 24px;
  height: 24px;
  fill: var(--text-gray);
  transition: fill 0.2s;
}
.navElementsActive svg {
  fill: var(--main-color);
}
@media (max-width: 450px) {
  .navElements svg {
    width: 20px;
    height: 20px;
  }
  #navigationBar {
    height: 65px;
  }
  #addButton {
    bottom: 80px;
  }
}
@media (min-width: 740px) {
  #navigationBar {
    width: 80px;
    min-width: initial;
    height: 100vh;
    left: 0%;
    transform: initial;
    flex-direction: column;
    border-radius: 0px;
  }
}
