/************** RecentlyAdded Page **************/

#recentPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: padding, opacity;
}
#recentPageHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#recentHeaderTitle {
  color: white;
  margin: 0;
  margin-right: 30px;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
#recentBack {
  margin-left: 20px;
  width: 35px;
  height: 35px;
  fill: white;
  cursor: pointer;
}
#recentBack:hover {
  fill: var(--text-kinda-black);
}
#recentPageSelectMealContainer {
  background-color: var(--background-white);
  padding-bottom: 20px;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 30px;
  width: 95%;
  border-radius: 15px;
  -webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#recentPageSelectMealTitle {
  padding: 15px 20px;
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  margin: 0px;
  color: var(--text-kinda-black);
}
.recentAddItemsContainer {
  border-radius: 15px;
  overflow: hidden;
  width: 95%;
  margin: auto;
  background-color: var(--background-white);
  margin-bottom: 30px;
  -webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.recentAddItemsHeader {
  border-bottom: solid 2px var(--background-gray);
}
.recentAddItemsFooter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--background-white);
}
.recentAddItemsFooter p {
  margin: 0;
  padding: 8px 20px;
  font-weight: normal;
  color: var(--text-gray);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  text-align: right;
}
#recentDateText {
  display: inline-block;
  direction: ltr;
  text-align: right;
  margin: 0;
  padding: 5px 15px 5px 0;
  color: var(--text-gray);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
  margin: 10px 0;
}
.recentAddAll {
  float: left;
  text-align: right;
  margin: 0px;
  padding: 5px 0px 5px 15px;
  background-color: rgba(0, 0, 0, 0);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
  border: none;
  cursor: pointer;
  margin: 10px 0;
}
.recentAddAll:hover {
  color: var(--second-color);
}
#lottieEmpty {
  width: 50%;
  height: 50%;
  margin: auto;
  margin-top: 15px;
  margin-bottom: 15px;
}
#recentNoResults {
  text-align: center;
  margin-top: 15%;
  color: var(--text-gray);
}
@media (max-width: 450px) {
  #recentBack {
    width: 30px;
    height: 30px;
  }
}
