/************** Summary **************/

#summaryPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: opacity;
}
#summaryPageHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#summaryPageHeaderTitle {
  color: white;
  margin: 0;
  margin-right: 30px;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
/*kcal chart card*/
#summaryLastWeekKcalContainer {
  background-color: var(--background-white);
  padding: 20px;
  padding-bottom: 50px;
  margin: 25px 10px;
  border-radius: 15px;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#summaryLastWeekKcalTitle {
  text-align: center;
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
  margin: 0;
  margin-bottom: 50px;
}
#summaryLastWeekKcalNumContainer {
  display: inline-flex;
  flex-direction: column;
  vertical-align: top;
  justify-content: space-between;
  align-items: center;
  width: 13%;
  height: 200px;
}
#summaryLastWeekKcalNumContainer p {
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
}
#summaryLastWeekKcalNumContainer p:nth-child(1) {
  transform: translateY(-50%);
}
#summaryLastWeekKcalNumContainer p:nth-child(3) {
  transform: translateY(40%);
}
#summaryLastWeekKcalChartContainer {
  position: relative;
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-end;
  width: 85%;
  height: 200px;
  border-bottom: solid 1px var(--second-color);
  border-top: solid 1px var(--second-color);
  box-sizing: border-box;
  margin-right: 2%;
  z-index: 0;
}
.Kcalcharts {
  position: relative;
  height: 0%;
  max-height: 115%;
  min-height: 0.5%;
  width: 7%;
  background-color: var(--main-color);
  border: solid 1px var(--main-color);
  border-bottom: none;
  box-sizing: border-box;
}
.Kcalcharts p {
  position: absolute;
  bottom: 0px;
  left: 50%;
  margin: 0;
  transform: translate(-50%, 100%);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
}
.KcalchartsAnimation {
  transition: height 350ms ease-out;
  transition-delay: 0.5s;
}
.chartMiddleLine {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--second-color);
  z-index: -1;
  opacity: 0.5;
}
.chartQuarterLine {
  position: absolute;
  top: 25%;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--second-color);
  z-index: -1;
  opacity: 0.5;
}
.chart3QuarterLine {
  position: absolute;
  top: 75%;
  left: 0;
  width: 100%;
  height: 1px;
  background-color: var(--second-color);
  z-index: -1;
  opacity: 0.5;
}
