  margin-right: 2%;
  z-index: 0;
}
.Nutritionscharts {
  height: 100%;
  width: 7%;
  transform: rotate(180deg);
}
.Nutritionscharts div {
  width: 100%;
  height: 0;
  box-sizing: border-box;
}
.NutritionschartsAnimation div {
  transition: height 350ms ease-out;
  transition-delay: 0.5s;
}
.Nutritionscharts div:nth-child(1) {
  background-image: linear-gradient(to top, rgba(240, 152, 25, 0.5), rgba(255, 81, 47, 0.5));
  border-left: solid 1px #ff2a00;
  border-right: solid 1px #ff2a00;
}
.Nutritionscharts div:nth-child(2) {
  background-image: linear-gradient(to top, rgba(218, 34, 255, 0.5), rgba(151, 51, 238, 0.5));
  border-left: solid 1px #d400ff;
  border-right: solid 1px #d400ff;
}
.Nutritionscharts div:nth-child(3) {
  background-image: linear-gradient(to top, rgba(67, 100, 247, 0.5), rgba(0, 82, 212, 0.5));
  min-height: 1%;
  border-left: solid 1px #002fff;
  border-right: solid 1px #002fff;
  border-bottom: solid 1px #002fff;
}
.Nutritionscharts p {
  position: absolute;
  bottom: 0px;
  left: 50%;
  margin: 0;
  transform: translate(-50%, 100%);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
