.pie {
  position: relative;
  width: 35vw;
  height: 35vw;
  max-width: 200px;
  max-height: 200px;
  margin: auto 0px;
  border-radius: 50%;
}
.pie::after {
  content: ' ';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  height: 80%;
  background-color: var(--background-white);
  border-radius: 50%;
}
