/************** MainPage **************/

#MainPage {
  position: relative;
  margin: auto;
  width: 100%;
  min-width: 320px;
  max-width: var(--max-width);
  padding-bottom: 95px;
  text-align: center;
}
#header {
  position: sticky;
  z-index: 1;
}
#Gradient1 stop:nth-child(1) {
  stop-color: var(--main-color);
}
#Gradient1 stop:nth-child(2) {
  stop-color: var(--second-color);
}
#title {
  position: fixed;
  top: 25px;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  color: white;
  margin: 0;
  margin-top: -5px;
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
}
#remainProgress {
  width: 35%;
}
.remainValues {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  position: absolute;
  top: 37%;
  transform: translate(0, -50%);
}
.caloriesRemain,
.caloriesEaten {
  margin: auto;
  text-align: center;
  font-weight: bold;
  font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
  color: white;
  width: 100px;
}
.caloriesRemain p,
.caloriesEaten p {
  margin: 0px;
  margin-bottom: 0px;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
}
.caloriesRemain p:nth-child(1),
.caloriesEaten p:nth-child(1) {
  font-family: sans-serif;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  overflow: hidden;
}
#macrosPercent {
  position: absolute;
  width: 100%;
  display: flex;
  justify-content: space-around;
  color: white;
  font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
.macrosContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 29%;
}
.macrosPercentContainer {
  position: relative;
  width: 100%;
  height: 6px;
  border-radius: 5px;
  background-color: rgb(250, 250, 245, 0.5);
  overflow: hidden;
}
.macrosPercentLine {
  position: absolute;
  right: 0px;
  top: 0px;
  height: 6px;
  width: 0;
  max-width: 100%;
  background-image: linear-gradient(-225deg, #fffeff 0%, #d7fffe 100%);
  transition: width 500ms ease;
}
.macroName {
  margin: 0px;
  margin-bottom: 5px;
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
}
.percentNum {
  margin: 0;
  margin-top: 5px;
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  overflow: hidden;
}
.percentNum span {
  margin: 3px;
}
@keyframes arrowDailyDetailsAnim {
  20%,
  100% {
    bottom: 20px;
    transform: scale(1);
  }
  0% {
    bottom: 20px;
    transform: scale(1);
  }
  10% {
    bottom: 5px;
    transform: scale(0.6);
  }
}
#arrowDailyDetails {
  position: absolute;
  bottom: 20px;
  left: calc(50% - 20px);
  width: 40px;
  height: 40px;
  fill: white;
  cursor: pointer;
  animation: arrowDailyDetailsAnim 5s ease 1s infinite normal forwards;
}
#arrowDailyDetails:hover {
  fill: black;
}
#addButton {
  position: sticky;
  margin-left: 15px;
  bottom: 100px;
  float: left;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  z-index: 3;
  cursor: pointer;
  background-color: var(--main-color);
  box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
  transition: background-color 1000ms;
}
#addButton svg {
  width: 46%;
  height: 46%;
  position: absolute;
  top: 27%;
  left: 27%;
  /* transform: translate(-50%, -50%); */
  transform-origin: center;
}
@keyframes PulsAnimation {
  40%,
  100% {
    opacity: 0;
  }
  0% {
    opacity: 1;
  }
  30% {
    opacity: 0;
    transform: scale(1.4);
  }
}
#addButtonPulsAnimation1 {
  animation: PulsAnimation 4s ease 1s infinite normal forwards;
}
#addButtonPulsAnimation2 {
  animation: PulsAnimation 4s ease 1.3s infinite normal forwards;
}
#addButtonPulsAnimation1,
#addButtonPulsAnimation2 {
  position: absolute;
  top: clac(50% - 27.5px);
  left: clac(50% - 27.5px);
  width: 55px;
  height: 55px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
}
.circleButton {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  z-index: 3;
  cursor: pointer;
}
.floatButton {
  position: fixed;
  bottom: 60px;
  transform: translate(-50%, -50%) scale(0);
  text-align: center;
  background-color: var(--background-white);
  -webkit-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
  box-shadow: 3px 5px 10px -7px rgba(0, 0, 0, 0.55);
  /* display: none; */
}
.floatButton p {
  position: absolute;
  left: 50%;
  top: 60px;
  transform: translate(-50%);
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
}
.floatButton img {
  margin-top: 5px;
  width: 50px;
  height: 50px;
}
#sideAddFoodContainer {
  display: none;
  right: 0px;
  bottom: 0px;
  position: fixed;
  height: 100vh;
  max-height: 700px;
  width: calc((100% - var(--max-width)) / 2);
  flex-direction: column;
  justify-content: space-evenly;
}
#sideAddFoodContainer p {
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
}
.sideAddFood {
  background-color: var(--background-white);
  box-shadow: 3px 5px 10px -7px rgb(0 0 0 / 55%);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  z-index: 3;
  cursor: pointer;
  margin: auto;
  position: relative;
}
.sideAddFood img {
  margin-top: 5px;
  width: 50px;
  height: 50px;
}
#addFoodBG {
  position: fixed;
  top: 0;
  left: 0;
  margin: 0;
  width: 100vw;
  height: 100vh;
  z-index: 3;
  background-color: var(--background-kinda-white);
  display: none;
  opacity: 0;
  touch-action: none;
}
#dateSelector {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: var(--max-width);
  min-width: 320px;
}
#dateSelector label {
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-gray);
  direction: ltr;
  margin: 20px 0;
  transition: margin 500ms;
  display: flex;
  align-items: center;
}
.datepicker-toggle {
  display: inline-block;
  position: relative;
  width: 18px;
  height: 19px;
  margin-left: 10px;
}
.datepicker-toggle-button {
  position: absolute;
  left: 0;
  top: 0;
  width: 18px;
  height: 18px;
  fill: var(--text-gray);
}
.datepicker-input {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  box-sizing: border-box;
}
.datepicker-input::-webkit-calendar-picker-indicator {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
}
.dateArrow {
  padding: 10px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  fill: var(--main-color);
}
#tipsContainer {
  position: relative;
  margin: 10px auto;
  padding-top: 10px;
  width: 95%;
  background-color: azure;
  border-radius: 15px;
  background: linear-gradient(190deg, var(--main-color) 0%, var(--second-color) 100%);
  overflow: hidden;
  text-overflow: clip;
  -webkit-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  -moz-box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#tipsContainer svg {
  position: absolute;
  width: 15px;
  height: 15px;
  right: 15px;
  top: 15px;
  fill: white;
  cursor: pointer;
}
#tipsContainer svg:hover {
  fill: var(--text-kinda-black);
}
#tipsContainer p:nth-child(2) {
  margin: 0;
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  color: wheat;
  font-weight: bold;
}
#tipsContainer p:nth-child(3) {
  margin: auto;
  text-align: justify;
  padding: 0px 15px 15px 15px;
  color: white;
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
}
#diaryContainer {
  white-space: nowrap;
  overflow-x: hidden;
  width: 100%;
  margin: auto;
  padding-bottom: 75px;
  will-change: scroll-position;
}
.HideOnScrollElements {
  will-change: transform, opacity;
}
@media (max-width: 400px) {
  .floatButton img {
    margin-top: 8px;
    width: 45px;
    height: 45px;
  }
  .circleButton {
    width: 55px;
    height: 55px;
  }
  #addButton {
    width: 50px;
    height: 50px;
  }
  #addButtonPulsAnimation1,
  #addButtonPulsAnimation2 {
    top: clac(50% - 25px);
    left: clac(50% - 25px);
    width: 50px;
    height: 50px;
  }
  .dateArrow {
    width: 18px;
    height: 18px;
  }
}
@media (min-width: 740px) {
  #sideAddFoodContainer {
    display: flex;
  }
  #addButton {
    display: none;
  }
  .floatButton {
    display: none;
  }
}
