:root {
  --vw: 100vw;

  --max-width: 580px;
  --main-color: #088380;
  --second-color: #15cda8;

  --text-kinda-black: #2b2b2b;
  --text-white: white;
  --text-gray: #646464;

  --background-white: white;
  --background-gray: #f5f5f5;
  --background-kinda-white: #fafaf5;

  --vh: 100vh;
}
body {
  margin: 0;
  background-color: var(--background-gray);
  direction: rtl;
  font-family: 'Cairo', sans-serif;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  overscroll-behavior-y: contain;
  overscroll-behavior-x: none;
  letter-spacing: -0.5px;
  overflow: overlay;
}
a {
  all: unset;
}
input {
  -webkit-appearance: none;
  appearance: none;
}
select,
button {
  font-family: 'Cairo', sans-serif;
  cursor: pointer;
}
select option {
  background-color: var(--background-white);
}
input {
  font-family: 'Cairo', sans-serif;
  cursor: auto;
}
select:focus,
input:focus,
button:focus {
  outline: none;
}

/************** Firebaseui **************/

#firebaseui-auth-container {
  margin: auto;
}
.firebaseui-idp-text {
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-family: 'Cairo', sans-serif;
}
.firebaseui-idp-button,
.firebaseui-tenant-button {
  max-width: initial;
  border-radius: 50px;
  padding: 8px 25px;
}
.firebaseui-container {
  max-width: initial;
}
#accountchooser-iframe {
  display: none;
}
.progressbarsContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 45px 0;
}
.progressbarsContainer div {
  width: 25%;
}
