/************** ItemViewer **************/

#itemViewerPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: padding;
}
#itemViewerHeaderButtonsContainer {
  position: relative;
  width: 100%;
  height: 200px;
  background-color: var(--second-color);
}
#itemViewerPageHeader {
  position: sticky;
  top: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr) 0.3fr;
  grid-template-rows: auto;
  align-items: center;
  direction: ltr;
  padding: 20px;
  background-color: var(--second-color);
  z-index: 1;
}
#itemViewerHeaderTitle {
  color: white;
  margin: 0;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: end;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#itemViewerBackButton {
  fill: white;
  width: 35px;
  height: 35px;
  cursor: pointer;
}
#itemViewerBackButton:hover {
  fill: black;
}
@keyframes pulse {
  10% {
    transform: scale(1.2);
  }
}
#itemViewerFavouriteButton {
  fill: var(--text-white);
  width: 35px;
  height: 35px;
  justify-self: end;
  cursor: pointer;
  transform-origin: center;
  animation: pulse 1.5s infinite;
}
#itemViewerFavouriteButtonFill {
  transform: scale(0);
  fill: white;
  transform-origin: center;
  transition: all 300ms;
}

#itemViewerEdit {
  fill: white;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
#itemViewerEdit:hover {
  fill: black;
}
#itemViewerTrashCan {
  fill: white;
  width: 40px;
  height: 40px;
  margin-left: 10%;
  cursor: pointer;
}
#itemViewerTrashCan:hover {
  fill: black;
}
#itemViewerHeaderItemName {
  position: absolute;
  bottom: 10px;
  right: 20px;
  color: var(--text-white);
  margin: 0;
  font-weight: bold;
  font-size: calc(25px + 6 * ((var(--vw) - 320px) / 680));
  width: 75%;
}
#itemViewerHeaderItemName span {
  font-weight: normal;
  font-size: calc(20px + 6 * ((var(--vw) - 320px) / 680));
}
#itemViewerOkButton {
  fill: white;
  width: 40px;
  height: 40px;
  cursor: pointer;
}
#itemViewerOkButtonBg {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: -27px;
  left: 30px;
  width: 55px;
  height: 55px;
  border-radius: 50%;
  background-color: var(--main-color);
}
#itemViewerOkButtonAnimation1 {
  position: absolute;
  bottom: -27px;
  left: 30px;
  width: 55px;
  height: 55px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
  animation: PulsAnimation 4s ease 1s infinite normal forwards;
}
#itemViewerOkButtonAnimation2 {
  position: absolute;
  bottom: -27px;
  left: 30px;
  width: 55px;
  height: 55px;
  border: solid 1.5px var(--main-color);
  border-radius: 50%;
  box-sizing: border-box;
  pointer-events: none;
  animation: PulsAnimation 4s ease 1.3s infinite normal forwards;
}
#itemViewerInputsContainer {
  display: flex;
  align-items: baseline;
  justify-content: space-evenly;
  background-color: var(--background-white);
  padding-top: 60px;
  padding-bottom: 25px;
}
#itemViewerInput {
  padding: 0;
  width: 15%;
  height: 40px;
  color: var(--text-kinda-black);
  background-color: var(--background-white);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 0;
  border-bottom: solid 1px var(--text-kinda-black);
}
#itemViewerKcal {
  display: flex;
  align-items: baseline;
  color: var(--text-kinda-black);
  font-size: 30px;
  font-weight: bold;
  margin: 0;
  margin-bottom: 20px;
  padding: 0px 7% 25px 0;
  overflow: hidden;
}
#itemViewerKcal span {
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: normal;
  padding-right: 10px;
}

#itemViewerNutritionInfoTitle {
  color: var(--text-kinda-black);
  margin: 0;
  padding: 20px 20px 0 0;
  background-color: var(--background-white);
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
}
#itemViewerNutritionInfoSubTitle {
  color: var(--text-kinda-black);
  margin: 0;
  padding-right: 20px;
  background-color: var(--background-white);
  font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
}

#nutritionInfoPercentContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 45px 0;
  background-color: var(--background-white);
}
#nutritionInfoPercentContainer div {
  width: 22%;
}
.percentTitles {
  text-align: center;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
}
#reportButton {
  padding: 12px 0;
  margin: 20px 0;
  text-align: center;
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
  background-color: var(--background-white);
  display: flex;
  align-items: center;
  justify-content: center;
}
#reportButton:hover {
  cursor: pointer;
  background-color: var(--main-color);
  color: white;
}
#reportPopUpBG {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: var(--background-kinda-white);
  z-index: 2;
  display: none;
  opacity: 0;
}
#reportPopUp {
  display: none;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  width: 300px;
  height: 350px;
  background-color: var(--background-white);
  max-width: var(--max-width);
  transform: translate(-50%, -50%);
  box-shadow: 0px 0px 9px 2px rgb(0 0 0 / 20%);
  border-radius: 10px;
  text-align: center;
  z-index: 2;
  will-change: top;
}
#reportContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
}
#reportPopUp p {
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
}
#reportMessage {
  width: 80%;
  flex: 1;
  padding: 10px;
  margin-bottom: 20px;
  outline: none;
  resize: none;
  border: solid 1px var(--text-kinda-black);
  border-radius: 10px;
  background-color: var(--background-white);
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  font-family: 'Cairo', sans-serif;
  color: var(--text-kinda-black);
}
#sendReportButton {
  display: block;
  margin: auto;
  margin-bottom: 20px;
  width: 88%;
  border: none;
  border-radius: 10px;
  padding: 5px 0;
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: white;
  background-color: var(--main-color);
  background: linear-gradient(90deg, var(--main-color) 0%, var(--second-color) 75%);
  box-shadow: 0px 1px 10px -7px rgb(0 0 0 / 55%);
  cursor: pointer;
}
#sendReportButton:hover {
  background-color: var(--second-color);
  background: linear-gradient(90deg, var(--second-color) 0%, var(--main-color) 75%);
}
#sendReportButton:disabled {
  filter: grayscale();
  cursor: wait;
}
@media (max-width: 450px) {
  #itemViewerBackButton {
    width: 30px;
    height: 30px;
  }
  #itemViewerFavouriteButton {
    width: 30px;
    height: 30px;
  }
  #itemViewerEdit {
    width: 35px;
    height: 35px;
  }
  #itemViewerTrashCan {
    width: 35px;
    height: 35px;
  }
}
