/************** Measures **************/

#measuresPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  padding-bottom: 80px;
  will-change: opacity;
}
#measuresPageHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#measuresPageHeaderTitle {
  color: white;
  margin: 0;
  margin-right: 30px;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
.cardContainer {
  position: relative;
  background-color: var(--background-white);
  margin: 25px 10px;
  padding: 40px 20px;
  transform: perspective(700px);
  border-radius: 15px;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  will-change: transform;
}
@keyframes forMoreInformation1 {
  to {
    transform: scale(1);
  }
}
@keyframes forMoreInformation2 {
  to {
    width: 180px;
  }
}
@keyframes forMoreInformation3 {
  to {
    width: 30px;
  }
}
@keyframes forMoreInformation2Small {
  to {
    width: 160px;
  }
}
@keyframes forMoreInformation3Small {
  to {
    width: 26px;
  }
}
@keyframes forMoreInformation4 {
  to {
    transform: scale(0);
  }
}
@keyframes bmiInfoButton {
  0% {
    fill: white;
  }
  90% {
    fill: white;
  }
  100% {
    fill: var(--text-gray);
  }
}
#forMoreInfo {
  position: absolute;
  top: 9px;
  left: 9px;
  height: 30px;
  width: 30px;
  border-radius: 30px;
  background-color: var(--main-color);
  transform-origin: 50% 33%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: clip;
  transform: scale(0);
}
.forMoreInfoAnimation {
  animation: forMoreInformation1 300ms ease-out 1s forwards, forMoreInformation2 300ms ease-out 1.5s forwards,
    forMoreInformation3 300ms ease-out 3s forwards, forMoreInformation4 300ms ease-out 3.5s forwards;
}
#forMoreInfoPara {
  margin: auto;
  margin-right: 10px;
  color: white;
}
#forMoreInfoPara::after {
  content: "";
  position: absolute;
  top: 0;
  left:  0;
  width: 35px;
  height: 100%;
  background-color: var(--main-color);
}
.fillAnimation {
  animation: bmiInfoButton 2.9s linear 1s forwards;
}
.measureInfo {
  position: absolute;
  left: 10px;
  top: 10px;
  fill: var(--text-gray);
  width: 28px;
  height: 28px;
  cursor: pointer;
  transition: fill 5s;
}
.measureInfo:hover {
  fill: var(--main-color);
}
.measureInfoClose {
  display: none;
  position: absolute;
  right: 10px;
  top: 10px;
  fill: var(--text-gray);
  width: 28px;
  height: 28px;
  cursor: pointer;
}
.measureInfoClose:hover {
  fill: var(--main-color);
}
.mainTitles {
  font-size: calc(23px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
  margin: auto;
  margin-bottom: 30px;
  text-align: right;
}
.subTitles {
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
  margin: auto;
  margin-top: 25px;
  text-align: right;
}
.results {
  font-size: calc(25px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--main-color);
  margin: auto;
  margin-right: 20px;
  margin-top: 0;
  text-align: right;
}
.resultsUnits {
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
  display: inline;
  margin-right: 7px;
}
#bmiCard > .measureInfo {
  transition: fill 200ms;
}
#bmibar {
  position: relative;
  height: 7px;
  width: 95%;
  margin: 35px auto;
  margin-top: 40px;
  padding: 0px;
  padding-top: 3px;
  border-radius: 10px;
  background: linear-gradient(
    90deg,
    rgba(255, 0, 0, 1) 0%,
    rgba(255, 192, 0, 1) 50%,
    rgba(31, 255, 0, 1) 63%,
    rgba(255, 192, 0, 1) 75%,
    rgba(255, 0, 0, 1) 100%
  );
}
#bmiArrow {
  position: absolute;
  left: -5px;
  bottom: 3px;
  height: 4px;
  width: 0px;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-top: 14px solid var(--text-kinda-black);
}
.selectContainer {
  position: relative;
  border: solid 2px var(--main-color);
  border-radius: 10px;
  margin: 35px 0;
  text-align: center;
}
.selectTitle {
  position: absolute;
  top: -20px;
  right: 20px;
  padding: 0 5px;
  background-color: var(--background-white);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
  white-space: nowrap;
  height: 32px;
  margin: 0;
}
.infoPara {
  display: none;
  color: var(--text-kinda-black);
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  text-align: justify;
  margin: 0px;
  transform: rotateY(180deg);
  pointer-events: none;
}
.infoPara span {
  display: block;
  text-align: center;
  font-size: calc(21px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--main-color);
  font-weight: bolder;
}
@media (max-width: 400px) {
  .measureInfo {
    width: 24px;
    height: 24px;
  }
  .measureInfoClose {
    width: 24px;
    height: 24px;
  }
  #forMoreInfo {
    height: 26px;
    width: 26px;
    animation: forMoreInformation1 300ms ease-out 1s forwards, forMoreInformation2Small 300ms ease-out 1.5s forwards,
      forMoreInformation3Small 300ms ease-out 3s forwards, forMoreInformation4 300ms ease-out 3.5s forwards;
  }
  #forMoreInfoPara {
    font-size: 14px;
    margin-right: 28px;
  }
}
