.nutritionTableInfoDetail {
  display: flex;
  justify-content: space-between;
  padding-bottom: 20px;
  color: var(--text-kinda-black);
}
.nutritionTableInfoTitles {
  width: 50%;
}
.nutritionTableInfoMain {
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  padding: 0 13px;
}
.nutritionTableInfoSub {
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  margin-top: 2px;
  margin-bottom: 2px;
  padding: 0 13px;
}
.nutritionTableInfoValues {
  width: 50%;
}
.nutritionTableInfoValues p {
  text-align: left;
}