/************** CreateFood **************/

#createFoodPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  background-color: var(--background-white);
  will-change: padding;
}
#createFoodPageHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#createFoodBack {
  margin-left: 20px;
  width: 35px;
  height: 35px;
  fill: white;
  cursor: pointer;
}
#createFoodBack:hover {
  fill: black;
}
#createFoodHeaderButtonsContainer {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  color: white;
  overflow: hidden;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  cursor: pointer;
  text-align: center;
  margin: 0;
  margin-right: 15px;
}
#CreateMealTrashCan {
  fill: white;
  width: 40px;
  height: 40px;
  margin: 0 20px;
  cursor: pointer;
}
#CreateMealTrashCan:hover {
  fill: black;
}
#CreateMealSave {
  fill: white;
  width: 40px;
  height: 40px;
  margin-left: 20px;
  margin-right: 1%;
  cursor: pointer;
}
#CreateMealSave:hover {
  fill: black;
}
.createFoodItemsContainer {
  position: relative;
  border: solid 2px var(--main-color);
  border-radius: 10px;
  margin: 35px auto;
  width: 90%;
}
.createFoodTitles {
  position: absolute;
  top: -20px;
  right: 20px;
  padding: 0 5px;
  background-color: var(--background-white);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
  white-space: nowrap;
  height: 32px;
  margin: 0;
}
.createFoodUnits {
  position: absolute;
  left: 15px;
  bottom: 50%;
  transform: translate(0, 50%);
  margin: 0;
  text-align: center;
  color: var(--text-gray);
}
#foodName {
  display: block;
  margin: 0.5px auto;
  height: 55px;
  width: 99%;
  color: var(--text-kinda-black);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0);
}
#foodBrand {
  display: block;
  margin: 0.5px auto;
  height: 55px;
  width: 99%;
  color: var(--text-kinda-black);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0);
}
#foodKcal {
  display: block;
  margin: 0.5px auto;
  height: 55px;
  width: 99%;
  color: var(--text-kinda-black);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0);
}
#foodServingValue {
  display: block;
  margin: 0.5px auto;
  height: 55px;
  width: 99%;
  color: var(--text-kinda-black);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0);
}
#createFoodNutritionInfoDetail {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  padding-bottom: 20px;
  background-color: var(--background-white);
}
.createFoodNutritionInfoSub {
  font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
  margin: 10px 0;
  color: var(--text-kinda-black);
  height: 35px;
}
.createFoodNutritionInfoMain {
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: var(--text-kinda-black);
  margin: 20px 0;
  height: 35px;
}
#createFoodNutritionInfoValues {
  direction: ltr;
}
#createFoodNutritionInfoValues input {
  padding: 0px;
  width: 25%;
  margin-left: 5px;
  color: var(--text-kinda-black);
  background-color: var(--background-white);
  font-weight: bold;
  font-size: inherit;
  text-align: center;
  border: none;
  border-bottom: solid 1.5px var(--main-color);
  border-radius: 0px;
}
.createFoodDoneContainer {
  padding: 30px 0px;
  background-color: var(--background-kinda-white);
}
.createFoodDone {
  all: unset;
  display: block;
  margin: auto;
  width: 90%;
  color: white;
  background-color: var(--main-color);
  background: linear-gradient(90deg, var(--main-color) 0%, var(--second-color) 75%);
  text-align: center;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  padding: 15px 0;
  border-radius: 10px;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
.createFoodDone:hover {
  background-color: var(--second-color);
  background: linear-gradient(90deg, var(--second-color) 0%, var(--main-color) 75%);
}
@media (max-width: 450px) {
  #CreateMealTrashCan {
    width: 35px;
    height: 35px;
  }
  #CreateMealSave {
    width: 35px;
    height: 35px;
  }
}
