.menu-button {
  all: unset;
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid var(--text-kinda-black);
  margin: auto;
  padding-bottom: 5px;
  cursor: pointer;
}
.menu-button p {
  margin: 0;
  padding: 0;
  color: var(--text-kinda-black);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  padding-right: 10px;
}
.menu-button .centered {
  width: 100%;
  text-align: center;
  margin-right: 30px;
}
.menu-button svg {
  width: 30px;
  height: 30px;
  fill: var(--text-kinda-black);
  margin-inline: 10px;
}
.menu-popup {
  position: fixed;
  width: 80%;
  max-width: calc(var(--max-width) - 10%);
  border: none;
  background-color: var(--background-white);
  border-radius: 15px;
  padding: 0;
  box-shadow: 5px 5px 7px -6px rgba(0, 0, 0, 0.55);
}
@media (hover: hover) and (pointer: fine) {
  .menu-popup::-webkit-scrollbar {
    width: 14px;
  }
  .menu-popup::-webkit-scrollbar-track {
    box-shadow: inset 0 0 14px 14px transparent;
    border: solid 4px transparent;
  }
  .menu-popup::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 14px 14px #b3b3b3;
    border: solid 4px transparent;
    border-radius: 14px;
  }
  .menu-popup::-webkit-scrollbar-button {
    display: none;
  }
}
.menu-popup::backdrop {
  background: rgba(0, 0, 0, 0.5);
  animation: BGshow 0.2s ease normal;
}
.menu-popup.menu-closing::backdrop {
  animation: BGhide 0.2s ease normal;
}
@keyframes BGshow {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes BGhide {
  to {
    opacity: 0;
  }
}
.menu-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  cursor: pointer;
}
.menu-item:not(:last-child) {
  border-bottom: solid 2px var(--background-gray);
}
.menu-item:hover {
  background-color: var(--main-color);
}
.menu-item:hover p {
  color: white;
}
.menu-item:hover svg {
  fill: white;
}
.menu-item p {
  color: var(--text-kinda-black);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  margin: 0;
  user-select: none;
}
.menu-item svg {
  width: 24px;
  height: 24px;
}
