/************** CreateMeal **************/

#CreateMealPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  will-change: padding;
}
#CreateMealPageHeader {
  position: sticky;
  top: 0;
  z-index: 1;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#CreateMealBack {
  margin-left: 20px;
  width: 35px;
  height: 35px;
  fill: white;
  cursor: pointer;
}
#CreateMealBack:hover {
  fill: black;
}
#CreateMealHeaderTitle {
  color: var(--text-white);
  margin: 0 30px;
  overflow: hidden;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
#CreateMealHeaderDone:hover {
  color: var(--text-kinda-black);
}
#CreateMealNameInput {
  background-color: var(--background-white);
  margin: 15px 0px;
  padding: 5px 20px;
}
#CreateMealNameInputContainer {
  position: relative;
  border: solid 2px var(--main-color);
  border-radius: 10px;
  margin: 35px 0;
}
#CreateMealNameInputContainer p {
  position: absolute;
  top: -20px;
  right: 20px;
  padding: 0 5px;
  background-color: var(--background-white);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
  white-space: nowrap;
  height: 32px;
  margin: 0;
}
#CreateMealNameInputContainer input {
  display: block;
  margin: 0.5px auto;
  height: 50px;
  width: 100%;
  color: var(--text-kinda-black);
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  text-align: center;
  border: none;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0);
}
#CreateMealNameInputContainer input::placeholder {
  font-size: 18px;
  font-weight: normal;
}
#CreateMealCards {
  margin-top: 15px;
  margin-bottom: 15px;
  padding-bottom: 20px;
  background-color: var(--background-kinda-white);
}
#createMealNoFoods {
  text-align: center;
  padding: 20px 0px;
  margin: 0px;
  color: var(--text-gray);
}
.createMealEmpty {
  width: 30%;
  margin: auto;
}
#addFoodToMeal {
  all: unset;
  display: block;
  width: 90%;
  border: solid 2px var(--main-color);
  border-radius: 10px;
  margin: auto;
  margin-top: 20px;
  color: var(--text-kinda-black);
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  line-height: 50px;
  padding: 1px 0;
  font-weight: bold;
  cursor: pointer;
}
#addFoodToMeal:hover {
  background-color: var(--main-color);
  color: white;
}
#addFoodToMeal:hover svg {
  fill: white;
}
#addFoodToMeal svg {
  width: 20px;
  height: 20px;
  fill: var(--main-color);
  vertical-align: middle;
  margin-left: 25px;
  margin-right: 20px;
}
#CreateMealDone {
  color: white;
  background-color: var(--main-color);
  background: linear-gradient(90deg, var(--main-color) 0%, var(--second-color) 75%);
  text-align: center;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  padding: 10px 0;
  border-radius: 10px;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
#CreateMealDone:hover {
  background-color: var(--second-color);
  background: linear-gradient(90deg, var(--second-color) 0%, var(--main-color) 75%);
}
#CreateMealNutritionInfoContainer {
  background-color: var(--background-kinda-white);
}
#CreateMealNutritionInfoTitle {
  color: var(--text-kinda-black);
  margin: 0;
  padding: 20px 20px 0 0;
  background-color: var(--background-white);
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
}
#CreateMealNutritionInfoSubTitle {
  color: var(--text-kinda-black);
  margin: 0;
  padding-right: 20px;
  background-color: white;
  font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
}

#CreateMealNutritionInfoPercentContainer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 45px 0;
  background-color: var(--background-white);
}
#CreateMealNutritionInfoPercentContainer div {
  width: 22%;
}
.CreateMealPercentTitles {
  text-align: center;
  margin: 0;
  color: var(--text-kinda-black);
  font-size: calc(13px + 6 * ((var(--vw) - 320px) / 680));
}
@media (max-width: 450px) {
  #CreateMealBack {
    width: 30px;
    height: 30px;
  }
}
