/************** Settings **************/

#settingsPage {
  width: 100%;
  max-width: var(--max-width);
  min-width: 320px;
  margin: auto;
  padding-bottom: 100px;
  will-change: opacity;
}
#settingsPageHeader {
  position: sticky;
  top: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 70px;
  background-color: var(--main-color);
}
#UserContainer {
  position: relative;
  background-color: var(--background-white);
  margin: 0 10px;
  margin-top: 30px;
  padding: 20px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
#userPicAnimation {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: auto;
}
#userPic {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 64px;
  height: 64px;
  border-radius: 50%;
  margin: auto;
  background-image: url(../assets/userBG.png);
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
}
#userName {
  text-align: center;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
}
#logOut {
  position: relative;
  display: block;
  width: 70%;
  border: none;
  border-radius: 10px;
  margin: auto;
  padding: 5px 0px;
  font-weight: bold;
  font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  color: white;
  background-color: var(--main-color);
  background: linear-gradient(90deg, var(--main-color) 0%, var(--second-color) 75%);
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
  cursor: pointer;
}
#logOut svg {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 22px;
  height: 22px;
  vertical-align: middle;
  fill: white;
}
#settingsPageHeaderTitle {
  color: white;
  margin: 0;
  margin-right: 30px;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
}
.containerTitles {
  text-align: center;
  font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  color: var(--text-kinda-black);
}
#personalInfoContainer {
  position: relative;
  background-color: var(--background-white);
  margin: 0 10px;
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
}
.settingsItemsContainer {
  position: relative;
  border: solid 2px var(--main-color);
  border-radius: 10px;
  margin: 35px 0;
  text-align: center;
}
#weightGoal {
  font-size: calc(16px + 6 * ((var(--vw) - 320px) / 680));
  font-weight: bold;
  line-height: 50px;
  padding: 1px 0;
  margin: auto;
  height: 50px;
  text-align: center;
  color: var(--text-kinda-black);
}
.settingsUnits {
  position: absolute;
  left: 15px;
  bottom: 50%;
  transform: translate(0, 50%);
