.input-range-container {
  position: relative;
  margin: 4.6px auto 20px;
  width: 90%;
  outline: none;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}
.input-range-progress {
  position: absolute;
  left: -2px;
  top: 25%;
  width: 100%;
  height: 11px;
  border-top-left-radius: 9px;
  border-bottom-left-radius: 9px;
  background-color: white;
  background: linear-gradient(90deg, rgba(255, 255, 255, 1) 92%, rgba(0, 0, 0, 0) 100%);
  pointer-events: none;
  outline: none;
}
input[type='range'].settingSlider {
  direction: ltr;
  width: 100%;
  margin-bottom: 20px;
  background-color: transparent;
  -webkit-appearance: none;
}
input[type='range'].settingSlider:focus {
  outline: none;
}
input[type='range'].settingSlider::-webkit-slider-runnable-track {
  background: rgba(0, 0, 0, 0.3);
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 9px;
  width: 100%;
  height: 10.8px;
  cursor: pointer;
}
input[type='range'].settingSlider::-webkit-slider-thumb {
  margin-top: -4.6px;
  width: 20px;
  height: 20px;
  background: #ffffff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  -webkit-appearance: none;
  box-shadow: 0 0 2px black;
}
input[type='range'].settingSlider::-moz-range-track {
  background: rgba(0, 0, 0, 0.3);
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 9px;
  width: 100%;
  height: 10.8px;
  cursor: pointer;
}
input[type='range'].settingSlider::-moz-range-thumb {
  width: 20px;
  height: 20px;
  background: #ffffff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
input[type='range'].settingSlider::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 5px 0;
  color: transparent;
  width: 100%;
  height: 10.8px;
  cursor: pointer;
}
input[type='range'].settingSlider::-ms-fill-lower {
  background: #000000;
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 18.2px;
}
input[type='range'].settingSlider::-ms-fill-upper {
  background: rgba(0, 0, 0, 0.3);
  border: 0px solid rgba(0, 0, 0, 0);
  border: 0;
  border-radius: 18.2px;
}
input[type='range'].settingSlider::-ms-thumb {
  width: 20px;
  height: 20px;
  background: #ffffff;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type='range'].settingSlider:focus::-ms-fill-lower {
  background: rgba(0, 0, 0, 0.3);
}
input[type='range'].settingSlider:focus::-ms-fill-upper {
  background: #000000;
}
/*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
  how to remove the virtical space around the range input in IE*/
@supports (-ms-ime-align: auto) {
  /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
  input[type='range'].settingSlider {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}

